import { get, post } from '@Green-Dot-Corporation/eureka-lib-fetch-utils';

export default class GatewayHttpClient {
    // Set the accessToken at the first place
    static accessToken = '';

    static #addTokenOptions(options) {
        const { header: headers } = options ?? {};
        const tokenAddedHeader = { ...headers };

        if (!tokenAddedHeader.Authorization) {
            tokenAddedHeader.Authorization = `Bearer ${GatewayHttpClient.accessToken}`;
        }
        const tokenAddedOptions = { ...options };
        tokenAddedOptions.headers = tokenAddedHeader;

        return tokenAddedOptions;
    }

    static get = async (url, options) => {
        const tokenAddedOptions = GatewayHttpClient.#addTokenOptions(options);
        const resp = await get(url, tokenAddedOptions);

        return resp;
    };

    static post = async (url, options) => {
        const tokenAddedOptions = GatewayHttpClient.#addTokenOptions(options);
        const resp = await post(url, tokenAddedOptions);

        return resp;
    };
}
