import { get } from '@Green-Dot-Corporation/eureka-lib-fetch-utils';
import GatewayHttpClient from '../util/GatewayHttpClient';

export const chatUrls = {
    getConfigUrl: '/v1/api/gbr/config',
    leaveMessageUrl: '/v1/api/gbr/leaveagentmessage',
};
export default {
    async getChatConfig() {
        const config = await get(chatUrls.getConfigUrl);

        return config;
    },
    async leaveMessage(leaveMessageRequest) {
        const resp = await GatewayHttpClient.post(
            chatUrls.leaveMessageUrl,
            leaveMessageRequest,
        );

        return resp;
    },
};
