import { getThemeCSS } from '@Green-Dot-Corporation/eureka-lib-theme-utils';
import { get } from '@Green-Dot-Corporation/eureka-lib-fetch-utils';

export default class CssImport {
    static #getBrandDesignToken = async (brand) => {
        // TODO: Add retrieve the brand design token logic here
        if (brand === 'go2bank') {
            const designToken = await get('/go2bank.design.json');

            return designToken;
        }
        throw new Error(`Not support brand: "${brand}"`);
    };

    static import = async (brand) => {
        const designToken = await CssImport.#getBrandDesignToken(brand);
        const cssVariable = getThemeCSS(designToken);
        const stylesheet = document.createElement('style');
        stylesheet.appendChild(document.createTextNode(cssVariable));
        document.head.appendChild(stylesheet);
    };
}
