import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import WindowContainer from '../window-container/WindowContainer';
import Option from './Option';
import Help from '../svg/Help';
import Support from '../svg/Support';
import Message from '../svg/Message';
// Styles
import './styles/choose-option-window.scss';

class ChooseOptionWindow extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        headerTitle: PropTypes.string,
        ewt: PropTypes.number,
    };

    static defaultProps = {
        // TODO: replace the title when localization available
        headerTitle: 'How can we help?',
    };

    render() {
        const { contextCls } = this.props;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
            >
                <div className={`${this.baseCls}__header-container`}>
                    {this.renderSvg()}
                    {this.renderHeader()}
                </div>

                {this.renderOptions()}
            </WindowContainer>
        );
    }

    renderSvg = () => {
        const { contextCls } = this.props;

        return (
            <Help
                contextCls={cx(
                    `${this.baseCls}__header-container-svg`,
                    contextCls,
                )}
            />
        );
    };

    renderHeader = () => {
        const { headerTitle } = this.props;

        return (
            <div
                className={`${this.baseCls}__header-container-title`}
                aria-label={headerTitle}
            >
                {headerTitle}
            </div>
        );
    };

    renderOptions = () => {
        const { ewt } = this.props;

        return (
            <div className={`${this.baseCls}__content-container`}>
                {/* TODO: replace the title and meta when localization available */}
                <Option
                    contextCls={`${this.baseCls}__option-support`}
                    shouldShowAngle
                    text="Chat with a support agent"
                    meta={`Estimated wait time ${ewt} minutes.`}
                    onClick={this.handleSupportOptionClick}
                    renderSvg={this.renderSvgSupport}
                />
                {/* TODO: replace the title and meta when localization available */}
                <Option
                    contextCls={`${this.baseCls}__option-message`}
                    shouldShowAngle
                    text="Leave a message"
                    meta="Describe your issue and we will get back to you."
                    onClick={this.handleMessageOptionClick}
                    renderSvg={this.renderSvgMessage}
                />
            </div>
        );
    };

    renderSvgSupport = () => (
        <Support contextCls={`${this.baseCls}__svg-container`} />
    );

    renderSvgMessage = () => (
        <Message contextCls={`${this.baseCls}__svg-container`} />
    );

    handleCloseBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleSupportOptionClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('support option click');
    };

    handleMessageOptionClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('message option click');
    };

    baseCls = 'choose-option-window';
}

export default ChooseOptionWindow;
